/* App.css */

/* Reset default margins and paddings */
body,
h1,
h2,
h3,
p {
  margin: 0;
  padding: 0;
}

/* Set background color for the whole page */
body {
  background-color: #f1f1f1;
}

/* Center the content vertically and horizontally */
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  font-family: Arial, sans-serif;
}

/* Style the header section */
.App-header {
  text-align: center;
  padding: 2rem;
  background-color: #ffffff;
}

.App-header h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.App-header p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
}

.App-header .App-logo {
  width: 100px;
  height: 100px;
  margin-bottom: 1.5rem;
}

.App-header a {
  color: #61dafb;
}

/* Style the About section */
.About {
  padding: 2rem;
  background-color: #ffffff;
}

.About h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.About p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
}

/* Style the Projects section */
.Projects {
  padding: 2rem;
  background-color: #f1f1f1;
}

.Projects h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.Project {
  margin-bottom: 2rem;
}

.Project h3 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.Project p {
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

/* Style the Contact section */
.Contact {
  padding: 2rem;
  background-color: #ffffff;
}

.Contact h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.Contact p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
}

/* Style the Footer */
.App-footer {
  background-color: #f1f1f1;
  padding: 1rem;
  text-align: center;
  font-size: 1rem;
}
